@import "../utils/variables.scss";

.chat {
  background-color: rgba(255, 255, 255, 0.384);
  height: 80vh;
  width: 500px;
  margin: 0 auto;
  margin-top: 20px;
  padding: 20px;
  border-radius: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
}

.chat__messages {
  background-color: #fff;
  border-radius: 10px;
  display: flex;
  max-height: 80%;
  flex-direction: column;
  background-color: rgba(255, 255, 255, 0.5);
  padding: 10px 0;
  overflow: scroll;
  overflow-x: hidden;
  margin-bottom: 20px;
}

.chat__messages::-webkit-scrollbar {
  width: 20px;
}

.chat__messages::-webkit-scrollbar-track {
  background-color: #e4e4e4a1;
  border-radius: 100px;
}

.chat__messages::-webkit-scrollbar-thumb {
  border-radius: 100px;
  // border: 6px solid #00b3db;
  border-left: 0;
  border-right: 0;
  background-color: #00b3db;
}

.chat__message {
  width: 500px;
  background-color: transparent;
  display: flex;
  justify-content: start;
  align-items: center;
  position: relative;

  &:not(:last-child) {
    margin-bottom: 20px;
  }
}

.message__bio {
  align-self: flex-start;
  display: flex;
  width: 16%;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.message__img {
  border-radius: 100%;
  border: 1px solid #000;
  width: 50px;
  height: 50px;
}

.message__diplayName {
  text-align: center;
  font-size: 13px;
  padding: 0;
  margin: 0;
}

.message__text {
  width: 70%;
  background-color: #e8027db6;
  border-radius: 5px;
  margin: 0;
  padding: 5px;
  word-wrap: break-word;
}

.message__text_right {
  margin-right: 10px;
  background-color: #fbd6069a;
}

.message__right {
  flex-direction: row-reverse;
  justify-content: end;
}

.chat__form {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
}

.chat__from_input {
  display: block;
  resize: none;
  padding: 5px;
  border-radius: 5px;
  border: none;
  background-color: rgba(255, 255, 255, 0.829);
  font-size: 14px;
  width: 85%;
  outline: none;
  height: 80px;

  &:focus {
    outline: none;
  }
}

.chat__from_button {
  background-color: rgba(255, 255, 255, 0.685);
  cursor: pointer;
  width: 55px;
  height: 55px;
  border-radius: 100%;
  display: flex;
  align-items: center;
  border: none;
  justify-content: center;
  transition: 0.2s;

  &:hover {
    box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
    transform: scale(1.02);
    background-color: rgba(255, 255, 255, 0.836);
  }

  &:hover .arrow {
    border-color: #00b3db;
  }

  &:hover .arrow::before {
    border-color: #00b3db;
  }
}

.arrow {
  display: block;
  border-top: 5px solid #00b3dbbb;
  border-right: 5px solid #00b3dbbb;
  width: 20px;
  height: 20px;
  transform: rotate(45deg);
  position: relative;
  transition: 0.2s;

  &::before {
    content: "";
    display: block;
    border-top: 5px solid #00b3dbbb;
    border-right: 5px solid #00b3dbbb;
    width: 15px;
    height: 15px;
    position: absolute;
    right: 5px;
    bottom: -5px;
    transition: 0.2s;
  }
}
